export default {
	data() {
		return {
			options: '',
			x: 162.5 + 'upx',
			tu: 'no',
			stars: 4,
			pages: 0,
			datas: '',
			num1: 0,
			num2: 0,
		}
	},
	onLoad(options) {
		var that = this;
		that.options = options;
		that.get_comment_list();
	},

	//监听用户下拉动作，一般用于下拉刷新
	onPullDownRefresh() {
		this.pages = 0;
		this.datas = '';
		this.get_comment_list();
	},
	//页面滚动到底部的事件 常用于上拉加载下一页数据
	onReachBottom() {
		this.get_comment_list();
	},
	methods: {
		change(e) {
			var that = this;
			var id = e.currentTarget.id;
			if (id == 0) {
				that.x = '162.5upx';
				that.tu = 'no';
				that.pages = 0;
				that.datas = '';
			} else {
				that.x = '506upx';
				that.tu = 'yes';
				that.pages = 0;
				that.datas = '';
			}
			that.get_comment_list();
		},
		yulan(e) {
			var current = e.currentTarget.dataset.url;
			uni.previewImage({
				current: current, // 当前显示图片的http链接
				urls: [current] // 需要预览的图片http链接列表
			})
		},
		get_comment_list() {
			var that = this;
			this.$http.post("comment/getcommentlist", {
				goods_id: that.options.goods_id,
				pages: that.pages,
				types: that.tu,
				froms: that.options.froms ? that.options.froms : 'shop'
			}).then((res) => {
				var datas = res.data.data;
				that.num1 = res.data.num1;
				that.num2 = res.data.num2;
				if (that.datas) {
					var newdata = that.datas.concat(datas)
					that.datas = newdata;
					that.pages = that.pages + 1;
				} else {
					that.datas = datas;
					that.pages = that.pages + 1;
				}
			})
		},
	}
}
